@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(""), url("../fonts/roboto/roboto-cyrillic-700.woff2") format("woff2"), url("../fonts/roboto/roboto-cyrillic-700.woff") format("woff");
  /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */ }

:root {
  --content-width: 1012px;
  --gutter: 15px;
  --mob-padding: 35px;
  --primary-text-color: #3c4148;
  --second-text-color: #ffffff;
  --active-text-color: #f29700;
  --primary-bg-color: #ffffff;
  --second-bg-color: #AEAEAE;
  --active-bg-color: #eeeeee;
  --second-active-bg-color: #f29700;
  --active-bg-color-dark: #3c4148;
  --slide-bg-color: #0e65b7;
  --active-border-color: #f29700; }

.container {
  max-width: calc(var(--content-width));
  padding: 0 var(--mob-padding);
  margin: 0 auto;
  background-color: var(--primary-bg-color); }

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1); }

* {
  box-sizing: border-box; }

html {
  font-family: "Arial"; }

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: var(--second-bg-color); }

img {
  display: block;
  max-width: 100%; }

.scroll-hidden {
  overflow: hidden; }

.search {
  position: relative;
  display: flex;
  align-items: center; }
  .search__input {
    position: relative;
    padding: 2px 25px 2px 2px;
    border-radius: 5px;
    outline: none;
    border: 2px solid var(--active-bg-color);
    background-image: url("../img/search-gray.svg");
    background-repeat: no-repeat;
    background-size: 16px;
    background-position: 95%; }
    .search__input:focus {
      border: 2px solid var(--active-border-color); }
  .search__icon {
    position: absolute;
    right: -13%;
    width: 16px;
    height: 16px;
    cursor: pointer; }

.logo {
  display: flex;
  align-items: center; }
  .logo__text-wrap {
    margin-left: 15px; }
  .logo__title {
    font-size: 28px;
    margin-top: 0;
    margin-bottom: 0;
    color: var(--active-text-color); }
  .logo__descr {
    font-size: 14px;
    margin-top: 0;
    margin-bottom: 0; }

.menu {
  margin: 0;
  padding-left: 70px;
  display: flex; }
  .menu__item {
    list-style-type: none; }
  .menu__link {
    height: 45px;
    display: block;
    text-decoration: none;
    padding: 15px 15px;
    font-weight: 700;
    color: var(--primary-text-color); }
    .menu__link:hover {
      background-color: var(--second-active-bg-color);
      color: var(--second-text-color); }
    .menu__link:focus {
      background-color: var(--active-bg-color-dark);
      color: var(--second-text-color); }

.slider {
  position: relative;
  height: 446px;
  width: 100%; }
  .slider__item {
    height: 100%;
    font-family: "Roboto";
    background-color: var(--slide-bg-color);
    background-image: url("../img/promo/slide-1.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center 5%; }
    .slider__item-inner {
      padding: 60px 0 90px 110px;
      background-image: url("../img/promo/promo-overlay.png");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: center; }
    .slider__item-subtitle {
      margin-top: 0;
      margin-bottom: 10px;
      display: inline-block;
      padding: 8px 9px;
      letter-spacing: 0.02em;
      text-transform: uppercase;
      background-color: var(--second-active-bg-color);
      color: var(--second-text-color);
      font-size: 16px; }
    .slider__item-title {
      margin-top: 0;
      margin-bottom: 20px;
      max-width: 500px;
      font-size: 60px;
      line-height: 48px;
      color: var(--second-text-color);
      text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3); }
    .slider__item-descr {
      margin: 0;
      max-width: 600px;
      font-family: Arial;
      font-size: 16px;
      line-height: 24px;
      font-weight: 700;
      text-shadow: 0px 1px 1px rgba(0, 0, 0, 0.3);
      color: #ffffff; }
  .slider__pagination {
    position: absolute;
    left: 50%;
    bottom: 40px;
    transform: translateX(-50%);
    display: flex;
    grid-column-gap: 10px; }
    .slider__pagination-item {
      display: block;
      width: 10px;
      height: 10px;
      opacity: 0.8;
      border: 1px solid #ffffff;
      cursor: pointer; }
      .slider__pagination-item:hover {
        background-color: var(--second-bg-color); }
      .slider__pagination-item_active {
        border: 1px solid var(--second-active-bg-color);
        background-color: var(--second-active-bg-color); }

.header {
  position: relative; }
  .header__inner {
    display: flex;
    flex-direction: column;
    background-color: var(--primary-bg-color); }
  .header__box {
    padding: 25px 0;
    display: flex;
    justify-content: space-between; }
  .header__nav::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 45px;
    height: 1px;
    width: 100%;
    background-color: var(--second-bg-color); }

.services {
  position: relative; }
  .services::after {
    content: "";
    position: absolute;
    top: 120px;
    height: 2px;
    width: 100%;
    background-color: var(--second-bg-color); }
  .services__items {
    padding-bottom: 115px;
    background-color: var(--primary-bg-color); }
    .services__items .row {
      margin-left: 0;
      margin-right: 0; }
  .services__item {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    width: 20%;
    max-height: 120px;
    padding: 45px 5px; }
    .services__item:hover {
      border-left: 1px solid var(--second-bg-color);
      border-right: 1px solid var(--second-bg-color);
      background-color: var(--active-bg-color); }
    .services__item-wrap {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      max-width: 36px;
      max-height: 42px; }
    .services__item-link {
      margin-left: 10px;
      color: var(--primary-text-color);
      max-width: 127px;
      font-family: Arial;
      font-size: 13px;
      line-height: 1.2;
      font-weight: 700;
      text-decoration: none; }
      .services__item-link::after {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        pointer-events: auto;
        z-index: 1; }
